.background {
  user-select: none;
  box-sizing: border-box;
  background-image: linear-gradient(0deg, rgba(0,0,0,0.65) 0%, rgba(0,0,0,0.45) 100%);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  overflow: hidden;
  height: 100vh;

  .container {
    display: flex;
    flex-grow: 1;
    align-items: center;
    justify-content: space-evenly;
    height: 100vh;

    .empty:hover {
      opacity: .6;
      transition: .3s;
    }

    .card {
      text-align: center;
      width: 245px;
      height: 356px;

      .content {
        padding: 0 2.5rem 4.5rem;

        .text {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          padding-top: 1.25rem;
          padding-bottom: .75rem;
        }

        .titleText {
          font-weight: 500;
          padding-top: .75rem;
          padding-bottom: 1.5rem;
        }

      }
    }
  }
}
.MuiDialogActions-root {
  justify-content: space-between!important;
  padding-left: 16px!important;
  padding-right: 16px!important;
}
.MuiDialogTitle-root {
  padding-top: 8px!important;
  padding-bottom: 8px!important;
}